.Toolbar{
    position: fixed;
    width: 100%;
    height: 56px;
    background-color: brown;
    color: yellow;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
   
}
@media only screen and (max-width:600px){
    .nav{
        display: none;   
    }
} 
.Menu{
    width: 40px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}
.Menu div {
    width: 90%;
    height: 3px;
    background-color: white;
}

@media (min-width: 600px) {
    .Menu {
        display: none;
    }
}


