.NavigationItem{
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    height:100;
    -ms-flex-align: center;
        align-items: center;
}

.NavigationItem  a {
    color:white ;
    text-decoration: none;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}

.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active{
    background-color: #8F5C2C;
    border-bottom: 4px solid lightcyan;
    color:white;
}


/* Mobile device */


@media(max-width:600px){
    .NavigationItem{
        margin: 0;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
    }
    
    .NavigationItem  a {
        color: white;
        text-decoration: none;
        padding: 16px 10px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        display: block;
        border-bottom: none;
    }
    
    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active{
        border-bottom: 4px solid lightcyan;
        color:white;
    }
} 


