.BuildControl {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    margin: 5px 0;
}

.BuildControlbutton {
    display: block;
    font: inherit;
    padding: 5px;
    margin: 0 5px;
    width: 80px;
    border: 1px solid #AA6817;
    cursor: pointer;
    outline: none;
}
.BuildControlbutton:disabled {
    background-color: #AC9980;
    border: 1px solid #7E7365;
    color: #ccc;
    cursor: not-allowed;
}

.BuildControlbutton:hover:disabled {
    background-color: #AC9980;
    color: #ccc;
    cursor: not-allowed;
}

.Label {
    padding: 10px;
    font-weight: bold;
    width: 80px;
}

.Less {  
    background-color: #D39952;
    color: white;
}

.More {
    background-color: #8F5E1E;
    color: white;
}
.Less:hover{  
    background-color: #DAA972;
    color: white;
}

.More:hover{
    background-color: #99703F;
    color: white;
}


