.BuildControls{
    width:100%;
    background-color: tomato;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 5px #ccc;
            box-shadow: 0 2px 5px #ccc;
    margin:auto;
    padding:10px 0;
}

.Order{
    margin: 20px 0px;
    width: 20%;
    background-color: yellow;
    font-size: larger;
    font-weight:bold;
}
.Order:hover{
    background-color: brown;
    color:yellow;
}

@-webkit-keyframes enable {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60%{
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100%{
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
.Order:disabled{
    cursor:not-allowed;
    background-color: #AC9980;
    color: #ccc;
}
